  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
  cursor: pointer;
}
.overlay.active {
  opacity: 0.2;
  visibility: visible;
}

.visualhidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
