.colored_bg {
  position: relative;
  padding-left: 10px;
  padding-right: 20px;
  color: #fff;
}
.colored_bg::after {
  content: "";
  position: absolute;
  top: -3px;
  left: 0;
  right: 15px;
  bottom: -3px;
  z-index: -1;
}
@media (max-width: 1300px) {
  .colored_bg::after {
    top: 3px;
    bottom: 3px;
  }
}
@media (max-width: 767px) {
  .colored_bg {
    padding: 0;
  }
  .colored_bg::after {
    top: 2px;
    left: -2px;
    right: -2px;
    bottom: 2px;
  }
}

.green_bg::after {
  background-color: #36b555;
}

.blue_bg::after {
  background-color: #1c69d4;
}

.fly__btn {
  display: none;
  position: fixed;
  bottom: 20px;
  left: 20px;
  right: 20px;
  z-index: 1;
  height: 60px;
  background-color: #36b555;
  transition: background-color 0.2s ease-out;
}
@media (hover: hover) {
  .fly__btn:hover {
    background-color: #55cc73;
  }
}
@media (max-width: 767px) {
  .fly__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
  }
  .fly__btn.show {
    display: flex;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}

.btn__red {
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 20px;
  color: #fff;
  background-color: #b01525;
  transition: background-color 0.2s ease-out;
}
@media (hover: hover) {
  .btn__red:hover {
    background-color: #e52034;
  }
}

.btn__arrow {
  justify-content: space-between;
  gap: 10px;
}
.btn__arrow::after {
  content: "";
  flex-shrink: 0;
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("../images/arrow-right-white.svg") center/contain no-repeat;
  transition: transform 0.2s ease-out;
}
@media (hover: hover) {
  .btn__arrow:hover::after {
    transform: translateX(5px);
  }
}

.btn__high {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 40px;
  padding: 44px 80px 44px 40px;
  font-weight: 400;
  font-size: 24px;
  transition: background-color 0.2s ease-out;
}
.btn__high::before {
  content: "";
  display: inline-block;
  width: 36px;
  height: 36px;
  transition: transform 0.2s ease-out;
}
@media (max-width: 1300px) {
  .btn__high {
    padding: 44px 50px 44px 40px;
  }
}
@media (max-width: 991px) {
  .btn__high {
    width: 100%;
