.header {
  position: relative;
  z-index: 6;
  background-color: #fff;
}
.header::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 4;
  width: 100%;
  height: 1px;
  background-color: #f2f2f2;
  pointer-events: none;
}

.header__container {
  z-index: 2;
  width: 100%;
  max-width: 1920px;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 20px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 991px) {
  .header__container {
    position: relative;
    overflow: hidden;
  }
  .header__container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4;
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
}
