}
.header__menu::before {
  content: "|";
  opacity: 0;
  pointer-events: none;
}
@media (max-width: 991px) {
  .header__menu {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    z-index: 3;
    display: block;
    padding-top: 80px;
    background-color: #fff;
    overflow-y: auto;
    transition: transform 0.2s ease-out;
  }
  .header__menu::before {
    display: none;
  }
  .header__menu.active {
    transform: translateX(100%);
  }
}
